import React from 'react';
import { $ }  from 'react-jquery-plugin'
import {Avatar, Grid,Link} from '@material-ui/core'
const boxStyle={margin:'30px'}
class UserProfile extends React.Component{
    constructor(props){
        super(props);
        this.state={
            user_name:'',
            user_id:'',
            icon:'',
            url:'',
            locaton:'',
            bio:''
        }
    }
    componentDidMount(){
        $.get(this.props.source,(result)=>{
            console.log(result);
            const data=result;
            if (data){
                this.setState({
                    user_name:data.login,
                    user_id:data.id,
                    icon:data.avatar_url,
                    url:data.html_url,
                    location:data.location,
                    bio:data.bio
                })
            }
        })
    }
    render(){
        return(
            <Grid align='center' style={boxStyle}>
                <Avatar src={this.state.icon} ></Avatar>
                <h3>Username:{this.state.user_name}</h3>
                <h3>ID:{this.state.user_id}</h3>
                <h3>Location:{this.state.location}</h3>
                <h3>Bio:{this.state.bio}</h3>
                <Link href={this.state.url}>Github Link</Link>
            </Grid>
            
        )
            
    }
}
export default UserProfile;